import { Button, Container, Typography } from "@material-ui/core";

import { Link, useHistory, useParams } from 'react-router-dom';


function Poista(props){

    const history = useHistory();
    const { id } = useParams();


    const poistaHavainto = () => {

        let tehtavatApu = props.tehtavat.filter((tehtava) => {
            return (tehtava.id !== id);            
        });

        props.setTehtavat([...tehtavatApu]);
        history.push("/");
      }

    return(

        <Container style={{width: "400px"}}>


            <Typography 
                variant="h5" 
                style={{marginTop: "20px"}}
            >
            Poista tehtävä "{props.tehtavat.filter((tehtava) => {
                                                        return (tehtava.id === id);
                                                    })[0].nimi}" ? 
            </Typography>


            <Button
                variant="contained"
                color="primary"
                fullWidth
                size="large"
                onClick={poistaHavainto}
                style={{marginTop: "20px"}}
            >
                Poista havainto
            </Button>

            <Button
                component={Link}
                to="/"
                variant="contained"
                color="default"
                fullWidth
                style={{marginTop: "20px"}}
            >
                Peruuta
            </Button>

        </Container>
    );
}

export default Poista;