import { Badge,Container, IconButton, List, ListItem, ListItemIcon, ListItemText, Typography } from "@material-ui/core";

import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import CheckBoxIcon from '@material-ui/icons/CheckBox'
import CheckBoxOutlineBlank from '@material-ui/icons/CheckBoxOutlineBlank';

import { Link } from 'react-router-dom';
import { format, formatDistanceStrict } from 'date-fns';
import differenceInDays from 'date-fns/differenceInDays';

function Aloitus(props) {

  const suoritettu = (indeksi) => {

    let tehtavatApu = [...props.tehtavat];

    tehtavatApu[indeksi].suoritettu = !tehtavatApu[indeksi].suoritettu;

    props.setTehtavat([...tehtavatApu]);

  }

    return(

        <Container style={{width: "1200px", textAlign: "center"}}>

          <Typography 
            variant="h4"
            style={{marginTop: "30px", marginBottom: "30px"}}
          >
            Palauttamattomat tehtävät:
          </Typography>

          <List>
            {props.tehtavat.sort((a, b) => {
              return(
                  a.deadline - b.deadline
              );
            }).map((tehtava, idx) => { 

              return (
                      <ListItem key={idx} style={{border: "1px solid black", width: "550px", margin: "10px",float: "left", height: "200px"}} >


                        <ListItemText>

                          <ListItemIcon style={{float: "left", marginRight: "50px"}}>
                            <IconButton 
                              onClick={ () => { suoritettu(idx) }}
                              component={Link}
                              to={`/poista/${tehtava.id}`}
                            >
                              { (tehtava.suoritettu) 
                                ? <CheckBoxIcon color="secondary" style={{fontSize: "50px"}} />
                                : (differenceInDays(tehtava.deadline, new Date()) <= 3)
                                  ? <Badge badgeContent="Kiireinen" color="error" >
                                      <CheckBoxOutlineBlank color="secondary" style={{fontSize: "50px"}} />
                                    </Badge>
                                  : <CheckBoxOutlineBlank color="primary" style={{fontSize: "50px"}} />
                              }
                            </IconButton>
                          </ListItemIcon>

                          <Typography 
                            variant="h6"
                            style={{marginTop: "30px"}}
                          >
                           Tehtävä: {tehtava.nimi}
                          </Typography>

                          <Typography>
                           Opintojakso: {tehtava.jakso} 
                          </Typography>

                          <Typography>
                           Deadline: {format(tehtava.deadline, "d.M.y HH:mm")}
                          </Typography>

                          <Typography 
                            style={{marginLeft: "10px"}}
                          >
                            {formatDistanceStrict(new Date(tehtava.deadline), new Date())}
                          </Typography>

                          <ListItemIcon style={{float: "right"}}>
                            <IconButton
                              component={Link}
                              to={`/poista/${tehtava.id}`}
                            >
                              <DeleteIcon />
                            </IconButton>
                          </ListItemIcon>

                          <ListItemIcon style={{float: "right"}}>
                            <IconButton
                              component={Link}
                              to={`/muokkaa/${tehtava.id}`}
                            >
                              <EditIcon />
                            </IconButton>
                          </ListItemIcon>

                        </ListItemText>

                      </ListItem >
                    )
            })}
        </List>

      </Container>

    );
}

export default Aloitus;