import Lisays from './components/Lisays';
import Valikko from './components/Valikko';
import Poista from './components/Poisto';
import Aloitus from './components/Aloitus';
import Muokkaa from './components/Muokkaa';

import { useState, useEffect } from 'react';

import { BrowserRouter as Router, Route } from 'react-router-dom';



function App() {

  const [tehtavat, setTehtavat] = useState([]);

  const tallennaTiedot = () => {

    localStorage.setItem("kouluTehtavat", JSON.stringify(tehtavat));
  } 
  
  const avaaKouluTehtavat = () => {

    if (localStorage.getItem("kouluTehtavat")) {

      setTehtavat(JSON.parse(localStorage.getItem("kouluTehtavat")));

    } else {
      setTehtavat([]);
    }    
  }

  useEffect(() => {

    avaaKouluTehtavat();

  }, []);

  useEffect( () => {

    tallennaTiedot();

  }, [tehtavat]);



  return (

    <Router>

      <Valikko tehtavat={tehtavat} setTehtavat={setTehtavat} />

      <Route path="/" exact>
        <Aloitus tehtavat={tehtavat} setTehtavat={setTehtavat} tallennaTiedot={tallennaTiedot}></Aloitus>
      </Route>

      <Route path="/lisays">
        <Lisays tehtavat={tehtavat} setTehtavat={setTehtavat} ></Lisays>
      </Route>

      <Route path="/poista/:id">
        <Poista tehtavat={tehtavat} setTehtavat={setTehtavat}></Poista>
      </Route>

      <Route path="/muokkaa/:id">
        <Muokkaa tehtavat={tehtavat} setTehtavat={setTehtavat}></Muokkaa>
      </Route>



    </Router>

  );
}

export default App;
