import { Button, Container, TextField, Typography } from "@material-ui/core";

import { Link, useHistory } from 'react-router-dom';
import { useState } from "react";
import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { v4 as uuid } from 'uuid';


function Lisays(props){

    const [uusiTehtavaNimi, setUusiTehtavaNimi] = useState();
    const [jaksoNimi, setJaksoNimi] = useState();
    const [aika, setAika] = useState(new Date());

    const history = useHistory();


    const lisaaTehtava = (uusiTehtava) => {

        let tehtavaApu = {
                            nimi : uusiTehtavaNimi,
                            deadline: aika.getTime(),
                            jakso: jaksoNimi,
                            id: uuid()
                         }
    
        props.setTehtavat([tehtavaApu, ...props.tehtavat]);

        console.log(props.tehtavat);
        history.push("/");
    }

    return(

        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={ fi }>
            
            <Container style={{width: "400px"}}>

                <Typography
                    variant="h4"
                    style={{marginTop: "30px", marginBottom: "30px"}}
                >
                    Lisää uusi tehtävä:
                </Typography>

                <Typography style={{marginTop: "20px"}}>Tehtävän nimi:</Typography>

                <TextField
                    variant="outlined"
                    fullWidth
                    placeholder="Syötä tehtävän nimi..."
                    onChange={ (e) => { setUusiTehtavaNimi(e.target.value); }}
                />

                <Typography style={{marginTop: "20px"}}>Opintojakso:</Typography>

                <TextField
                    variant="outlined"
                    fullWidth
                    placeholder="Syötä opintojakson nimi..."
                    onChange={ (e) => { setJaksoNimi(e.target.value); }}
                />

                <DateTimePicker 
                    label="Tehtävän deadline"
                    inputVariant="outlined"
                    format=" d.M.y HH.mm "
                    ampm={false}
                    style={{marginTop: "20px"}}
                    cancelLabel="Peruuta"           
                    disablePast={true}
                    value={aika}
                    onChange={setAika}
                />

                <Button
                    variant="contained"
                    color="primary"
                    fullWidth
                    size="large"
                    onClick={lisaaTehtava}
                    style={{marginTop: "20px"}}
                >
                    Lisää tehtävä
                </Button>

                <Button
                component={Link}
                to="/"
                variant="contained"
                color="default"
                fullWidth
                style={{marginTop: "20px"}}
                >
                    Peruuta
                </Button>

            </Container>
            
        </MuiPickersUtilsProvider>
    );
}

export default Lisays;