import { AppBar, Box, CssBaseline, Toolbar, Typography } from "@material-ui/core";

import { Link } from 'react-router-dom';

import ListIcon from '@material-ui/icons/List';
import AddIcon from '@material-ui/icons/Add';
import ImportContactsIcon from '@material-ui/icons/ImportContacts';

function Valikko(props) {

    return(

        <CssBaseline>

            <AppBar position="static" style={{alignItems: "center"}}>

                <Toolbar>
                    <Box style={{marginTop: "10px", marginRight: "300px", marginLeft: "600px"}}>
                        <ImportContactsIcon/>
                        <Typography variant="h3" style={{float: "left"}}>
                            Koulutehtävät
                        </Typography>
                    </Box>

                    <Box style={{float: "right", paddingTop: "30px"}}>
                        <Typography
                            variant="h6"
                        >
                            Palauttamattomia tehtäviä: {props.tehtavat.length}
                        </Typography>
                    </Box>
                </Toolbar>    

                <Toolbar>

                    <Box button component={Link} to="/" style={{float: "left", color: "inherit", marginLeft: "20px"}}>
                        <ListIcon style={{float: "left"}}/>
                        <Typography style={{float: "left", marginLeft: "5px"}}>
                            Tehtävälista
                        </Typography>
                    </Box>

                    <Box button component={Link} to="/lisays" style={{float: "left", color: "inherit", marginLeft: "20px"}}>
                        <AddIcon style={{float: "left"}}/>
                        <Typography style={{float: "left", marginLeft: "5px"}}>
                            Lisää tehtävä
                        </Typography>
                    </Box>

                </Toolbar>
                
            </AppBar>

        </CssBaseline>
       
    );
}

export default Valikko;