import { Button, Container, TextField, Typography } from "@material-ui/core";

import { Link, useHistory, useParams } from 'react-router-dom';
import { useState } from "react";
import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { v4 as uuid } from 'uuid';


function Muokkaa(props){

    const [uusiTehtavaNimi, setUusiTehtavaNimi] = useState();
    const [jaksoNimi, setJaksoNimi] = useState();

    const [muokattuAika, setMuokattuAika] = useState(new Date());

    const history = useHistory();
    const { id } = useParams();


    const muokkaaTehtavaa = () => {

        let poistoApu = props.tehtavat.filter((tehtava) => {
            return (tehtava.id !== id);            
        });

        let tehtavaApu = {
                            nimi : uusiTehtavaNimi,
                            deadline: muokattuAika.getTime(),
                            jakso: jaksoNimi,
                            id: uuid()
                         }
    
        props.setTehtavat([tehtavaApu, ...poistoApu]);
        history.push("/");
    }

    return(

        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={ fi }>
            <Container style={{width: "400px"}}>

                <Typography 
                    variant="h4"
                    style={{marginTop: "20px", marginBottom: "10px"}}
                >
                    Muokkaa tehtävää:
                </Typography>

                <Typography style={{marginTop: "20px"}}>Tehtävän nimi:</Typography>

                <TextField
                    variant="outlined"
                    fullWidth
                    placeholder="Syötä tehtävän nimi..."
                    onChange={ (e) => { setUusiTehtavaNimi(e.target.value); }}
                />

                <Typography style={{marginTop: "20px"}}>Opintojakson nimi:</Typography>

                <TextField
                    variant="outlined"
                    fullWidth
                    placeholder="Syötä opintojakson nimi..."
                    onChange={ (e) => { setJaksoNimi(e.target.value); }}
                />  

                <DateTimePicker 
                    label="Tehtävän deadline"
                    inputVariant="outlined"
                    format=" d.M.y HH.mm "
                    ampm={false}
                    style={{marginTop: "20px"}}
                    cancelLabel="Peruuta"           
                    disablePast={true}
                    value={muokattuAika}
                    onChange={setMuokattuAika}
                />

                <Button
                    variant="contained"
                    color="primary"
                    fullWidth
                    size="large"
                    onClick={muokkaaTehtavaa}
                    style={{marginTop: "20px"}}
                >
                    Tallenna tehtävä
                </Button>

                <Button
                component={Link}
                to="/"
                variant="contained"
                color="default"
                fullWidth
                style={{marginTop: "20px"}}
                >
                    Peruuta
                </Button>

            </Container>
        </MuiPickersUtilsProvider>
    );
}

export default Muokkaa;